/**
 * Theme:	Flat Bootstrap
 * File:	_base.scss
 *
 * The stylesheet for basic WordPress styles
 *
 * Contains padding, margins, list styles, etc.
 *
 * @package flat-bootstrap
 */
/*--------------------------------------------------------------
>>> TABLE OF CONTENTS:
----------------------------------------------------------------
# Typography
# Accessibility
# Alignments
# Clearings
# Content
  ## Header
  ## Sidebars
  ## Footer
  ## Posts and pages
  ## Asides (not used)
  ## Comments
# Media
  ## Captions and Galleries
  ## Profile Pictures ("avatars")
# Widgets & Plugins
  ## Widgets
  ## Plugins

--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
/*
 * Bootstrap handles basic typography and theme-specific typography should be set in the
 * theme's style.css
 */
/* Set some basic body styles */
body {
  -webkit-font-smoothing: antialiased;
  -webkit-overflow-scrolling: touch; }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/*
 * Text meant only for screen readers. Required only for WordPress as Bootstrap uses
 * .sr-only and we apply that in this theme.
 */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important; }

p.screen-reader-text {
  margin-bottom: 0; }

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 4px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-weight: 700;
  height: auto;
  line-height: normal;
  padding: 15px 23px 14px;
  position: absolute;
  left: 5px;
  top: 5px;
  text-decoration: none;
  text-transform: none;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar */ }

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
/*
 * Note that bootstrap.css has alignment as well (such as text-center, pull-left, pull-
 * right), but these are required by WordPress for all themes. Note we have included
 * versions both with a dash as well to make them easier for users to remember.
 */
.alignleft,
.align-left {
  display: inline;
  float: left;
  margin-right: 10px;
  /*1.5em*/ }

.alignright,
.align-right {
  display: inline;
  float: right;
  margin-left: 10px;
  /*1.5em*/ }

.aligncenter,
.align-center {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center; }

/*
 * Note that these were moved here from theme-flat.css in Flat Bootstrap v1.9.
 *
 * TO-DO: Consider deprecating this as Bootstrap already has text-center class
 */
.centered {
  text-align: center; }

.nocenter {
  text-align: left; }

/* For reference purposes, this is what core Bootstrap has for centering text
.text-center {
  text-align: center;
}
*/
/* For reference purposes, this is what core Bootstrap has for centering blocks
.center-block { // Bootstrap
  display: block;
  margin-left: auto;
  margin-right: auto;
}*/
/* For reference purposes, this is what WordPress core has
.aligncenter { // WordPress _S
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
*/
/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
/*
 * Note that bootstrap.css clears its floats. These are extra ones for WordPress.
 */
/* Clearing floats */
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.entry-meta:before,
.entry-meta:after,
.pager:before,
.pager:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
  content: '';
  display: table; }

.clear:after,
.entry-content:after,
.entry-meta:before,
.entry-meta:after,
.pager:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
  clear: both; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
/*
 * Limit the entire page for really wide screens, such as an iMac.
 *
 * TO-DO: Consider having #page wrap absolutely everything, so we wouldn't need to limit
 * page bottom, footer, etc.
 */
#page,
.navbar,
.after-footer {
  max-width: 1600px;
  margin: 0 auto; }

/* Prevent the page layout from breaking even if the user's CSS is bad */
#page {
  overflow: hidden; }

/*
 * Site Branding is a wrapper for both site title and description so they can be hidden
 * together more easily.
 */
/*.site-branding {
  padding-top: 3px;
  padding-bottom: 14px;
}*/
.site-title {
  margin-top: 25px; }

.site-description {
  font-size: 24px;
  margin-top: 10px;
  margin-bottom: 25px; }

.site-title a, .site-title a:hover, .site-title a:active {
  text-decoration: none; }

header.site-header.page-header {
  border-bottom: 0; }

.custom-header-text-color a:hover,
.custom-header-text-color a:focus {
  opacity: 0.75; }

.custom-header-image {
  margin: 0 auto;
  background-repeat: none;
  background-position: center center;
  max-width: 100%; }

.custom-header-image h1 {
  font-weight: 700; }

.custom-header-image h2 {
  font-size: 24px; }

/*
 * This is our scroll down arrow for a custom header on the home page, but also match it
 * to a carousel that might be at the top of a page instead.
 */
a.scroll-down,
a.scroll-down:hover span,
a.scroll-down:focus span,
.carousel-control:hover,
.carousel-control:focus {
  color: #fff;
  font-size: 20px; }

/*
 * This is our content header that is below the header and before the content area. Top
 * margin is set to 24px because Bootstrap already has 22px top margin on h1-h3 tags.
 * It also adds 11px bottom margin. So this equates to 46px top and bottom spacing.
 */
.content-header {
  padding-top: 24px;
  padding-bottom: 35px; }

header.content-header {
  min-height: 170px; }

.content-header i {
  font-size: 35px;
  text-align: center; }

/* Set initial image background so light text is readable before image loads */
.content-header-image {
  background-color: #e7e7e7; }

/*
 * These are our cover (home page) and section (other page) large content-header images.
 */
.section-image,
.cover-image {
  display: table;
  background-repeat: no-repeat;
  text-align: center;
  background-attachment: scroll;
  background-position: top center;
  background-color: #e7e7e7;
  /*gray*/
  width: 100%;
  position: relative;
  vertical-align: top;
  background-size: 100%;
  background-size: cover; }

.section-image-overlay,
.cover-image-overlay {
  display: table-cell;
  vertical-align: middle;
  overflow: hidden;
  padding: 50px;
  padding: 5%;
  z-index: 2; }

.cover-image,
.cover-image-overlay {
  height: 100%;
  /* For at least Firefox */
  min-height: 480px; }

.section-image,
.section-image-overlay {
  min-height: 340px; }

.section-image h1,
.cover-image h1 {
  font-size: 42px;
  color: #fff;
  line-height: 1.1; }

.section-image h2,
.cover-image h2 {
  font-size: 30px;
  color: #fff;
  line-height: 1.1; }

.section-image h3,
.section-image h4,
.section-image p,
.cover-image h2,
.cover-image h3,
.cover-image h4,
.cover-image p {
  color: #fff; }

.cover-image .spacer {
  margin-top: 100px; }

.section-featured h1 {
  font-size: 42px;
  line-height: 1.1; }

.section-featured h2 {
  font-size: 30px;
  line-height: 1.1; }

.section-featured {
  padding-top: 60px;
  border-bottom: #bdc3c7 solid 5px; }

/* For larger screens, increase font size and spacing */
@media (min-width: 768px) {
  .cover-image h1,
  .section-image h1,
  .wp-caption-text-fullwidth h1 {
    font-size: 70px;
    letter-spacing: 3px; }
  .cover-image h2,
  .section-image h2,
  .wp-caption-text-fullwidth h2 {
    font-size: 42px;
    letter-spacing: 2px; }
  .cover-image .spacer {
    margin-top: 200px; }
  .cover-image, .cover-image-overlay {
    min-height: 700px; }
  .section-image, .section-image-overlay {
    min-height: 400px; } }

/*--------------------------------------------------------------
## Sidebars
--------------------------------------------------------------*/
/* For main sidebar, add some whitespace */
#secondary {
  padding-bottom: 35px; }

#secondary aside.widget {
  margin-bottom: 10px;
  /* Heading will add 25px */ }

/* Get rid of bullets on sidebars, footer, page top, page bottom, and home page */
#secondary ul,
#sidebar-pagetop ul,
#sidebar-pagebottom ul,
#sidebar-home ul,
footer#colophon.site-footer ul {
  list-style: none;
  padding-left: 0; }

#secondary ul ul,
#sidebar-pagetop ul ul,
#sidebar-pagebottom ul ul,
#sidebar-home ul ul,
footer#colophon.site-footer ul ul {
  list-style: none;
  padding-left: 25px; }

#secondary ul li,
footer#colophon.site-footer ul li {
  margin-top: 10px; }

/* Make custom menus in page top, page bottom, and home page inline (horizontal). */
