#sidebar-pagetop ul > li,
#sidebar-pagebottom ul > li,
#sidebar-home ul > li {
    display: inline-block;
    padding-left: 5px;
    padding-right: 5px
}
.#sidebar-pagetop ul > li:first-child,
.#sidebar-pagebottom ul > li:first-child,
.#sidebar-home ul > li:first-child {
    padding-left: 0
}

#sidebar-pagetop .widget_nav_menu,
#sidebar-pagebottom .widget_nav_menu,
#sidebar-home .widget_nav_menu {
  margin-top: 11px;
}
*/
/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
/* Format the footer widget area */
.sidebar-footer {
  padding-top: 35px; }

.sidebar-footer h1,
.sidebar-footer h2,
.sidebar-footer h3,
.sidebar-footer h4,
.sidebar-footer h5,
.sidebar-footer h6 {
  margin-top: 11px;
  padding-bottom: 11px; }

.sidebar-footer aside.widget {
  margin-bottom: 35px; }

/* Format the "after footer", which has the footer nav menu and site credits */
.after-footer {
  padding-top: 15px;
  padding-bottom: 15px; }

/* Let's put dividers between footer menu items */
#footer-navigation ul.dividers > li:nth-child(n+2):before {
  content: "";
  display: inline-block;
  height: 10px;
  margin-right: 10px;
  border-left: 1px solid #dddddd; }

/*
 * Format the sample footer menu "top of page" icon. Since this stylesheet is WordPress
 * rather than relying on font-awesome, make the caret icon look the same. For some
 * reason the WordPress glyphicons are twice as large as the font-awesome ones.
*/
#footer-navigation .glyphicon-menu-up {
  font-size: 50%; }

/* Need to pad site credits to line up with the footer nav menu */
#site-credits {
  padding-top: 10px; }

/* Center the text after the footer for smartphones */
@media (max-width: 640px) {
  .footer-nav-menu, .site-credits {
    text-align: center;
    float: none !important; }
  .site-credits {
    padding-bottom: 15px; } }

/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
/* Content areas */
.content-area {
  padding-top: 35px; }

.content-area-wide .comments-area {
  padding-bottom: 35px; }

.page .entry-content {
  padding-top: 0; }

.entry-summary {
  margin-bottom: 25px; }

article.post {
  margin-bottom: 25px; }

/* When full-width pages are changed back to have a sidebar, fix the layout */
.content-area .entry-content .container {
  max-width: 100%;
  padding-left: 0;
  padding-right: 0; }

.content-area .entry-content .container .row {
  margin-left: 0;
  margin-right: 0; }

/* Entry meta */
footer.entry-meta,
div.entry-meta,
.edit-link {
  font-size: 16px; }

.edit-link a {
  color: #7f8c8d; }

.edit-link a:hover,
.edit-link a:focus {
  color: #3bc492; }

.cat-links, .tags-links {
  margin-right: 10px; }

.posted-on {
  margin-right: 5px; }

.by-line, .comments-link {
  margin-left: 5px;
  margin-right: 5px; }

/* Style tables like TwentyEleven theme with lines between entries */
.entry-content table,
.comment-content table {
  border-bottom: 1px solid #ddd;
  margin: 0 0 1.625em;
  width: 100%; }

.entry-content th,
.comment-content th {
  font-weight: 500;
  letter-spacing: 0.1em;
  line-height: 2.6em;
  text-transform: uppercase; }

.entry-content td,
.comment-content td {
  border-top: 1px solid #ddd;
  padding: 6px 10px 6px 0; }

/* Mark "sticky" (featured) posts as "Featured" */
.sticky .entry-title:after {
  content: "Featured";
  display: inine-block;
  font-size: 20px;
  margin-left: 15px;
  padding: 10px;
  vertical-align: middle; }

/* When sidebar not collapsed, add more padding. Make sure this is below both the #sidebar
 * and #posts and pages CSS sections as it applies to both
 */
@media (min-width: 992px) {
  .content-area {
    padding-bottom: 35px; }
  #secondary {
    padding-top: 35px; } }

/*--------------------------------------------------------------
## Asides
--------------------------------------------------------------*/
/*
 * This theme doesn't provide templates or styles for any post formats, but it's easy
 * to add them to a child theme
 */
/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
/* Get rid of bullets on comment <ul> and set some padding and indenting */
.comment-list,
.comment-list .children {
  list-style-type: none;
  padding: 0; }

/* Set child comment indent to a percentage for better responsive handling */
.comment-list .children {
  /*margin-left: 25px;*/
  margin-left: 5%; }

/* Style the standard WordPress comment form. Note Jetpack comments styled later. */
.comment-form [for="author"],
.comment-form [for="email"],
.comment-form [for="url"],
.comment-form [for="comment"] {
  float: left;
  width: 80px; }

.comment-form input[type="text"],
.comment-form input[type="email"],
.comment-form input[type="url"] {
  max-width: 310px;
  width: 70%; }

.comment-form,
.comment-textarea,
textarea {
  max-width: 100%;
  border-color: #ccc; }

/* Styles for default WordPress comments */
textarea#comment {
  max-width: 743px;
  /*largest width of content area w/sidebar*/
  width: 100%;
  max-height: 140px; }

/* Hide the label "Comment" above the comment box as its pretty obvious already */
.comment-form-comment label {
  display: none; }

p.comment-subscription-form {
  margin-bottom: 10px; }

.reply {
  margin-bottom: 25px; }

/* Get rid of the word "says" from comments */
.says {
  display: none; }

/* Space out pingbacks ("trackbacks") */
li.pingback {
  margin-bottom: 15px; }

/* Come on now, of course we need a margin before their name and comment */
.avatar {
  float: left;
  margin-right: 10px;
  margin-bottom: 10px; }

/*
 * Not used here, but required for inclusion by WordPress. Set this in the theme's
 * stylesheet if you want
 */
/* Set the comment box height via CSS for consistency across browsers */
form.contact-form textarea {
  height: 108px; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
/* Make sure these elements don't break the layout */
img, embed, iframe, media, swf, pre, code, container, select, .videopress-placeholder {
  /*display: block;*/
  max-width: 100%;
  overflow: hidden;
  white-space: pre;
  /* CSS 2.0 */
  white-space: pre-wrap;
  /* CSS 2.1 */
  white-space: pre-line;
  /* CSS 3.0 */
  white-space: -pre-wrap;
  /* Opera 4-6 */
  white-space: -o-pre-wrap;
  /* Opera 7 */
  white-space: -moz-pre-wrap;
  /* Mozilla */
  white-space: -hp-pre-wrap;
  /* HP Printers */
  word-wrap: break-word;
  /* IE 5+ */ }

/* Set images and videopress videos to auto-height to preserve proper aspect ratio */
img,
.videopress-placeholder {
  height: auto; }

/*--------------------------------------------------------------
## Captions and Galleries
--------------------------------------------------------------*/
/* Normal WordPress captions and galleries */
.wp-caption {
  max-width: 100%; }

.wp-caption-text,
.gallery-caption {
  text-shadow: none;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  /*margin-top: -22px;*/
  /* Offset Bootstrap .thumbnail class */
  margin-bottom: 22px; }

/* Center the thumbnails since WordPress centers the caption text */
.gallery-icon img {
  margin: auto; }

/* For standard WordPress slideshow, get rid of black background. We want to leave
 * whatever color background we have
 */
.slideshow-window {
  background-color: transparent !important;
  border: none !important; }

/* Style Bootstrap carousel's ("sliders") */
.carousel-inner img {
  margin-left: auto;
  margin-right: auto; }

.carousel-caption {
  max-width: 100%;
  text-shadow: none;
  padding-left: 10px;
  padding-right: 10px; }

/* Push the carousel indicators down below the image or content */
.carousel-indicators {
  bottom: -35px; }

/*
 * Get rid of gradient on Bootstrap carousel left and right buttons
 */
.carousel-control.left, .carousel-control.right {
  background-image: none;
  background-repeat: none; }

/*--------------------------------------------------------------
## Profile Pictures ("avatars")
--------------------------------------------------------------*/
/* Make user profile pics (avatars) circles */
.avatar {
  border-radius: 50%; }

img.avatar.thumbnail {
  border: none;
  padding: 0; }

/*--------------------------------------------------------------
# Widgets and Plugins
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Widgets
--------------------------------------------------------------*/
/*
 * Style the WordPress calendar widget because Bootstrap doesn't even add a 'table'
 * class to tables. Ugh.
 */
#calendar_wrap {
  font-size: 14px;
  max-width: 300px; }

.widget_calendar #wp-calendar {
  color: #555;
  width: 95%;
  text-align: center; }

.widget_calendar #wp-calendar caption,
.widget_calendar #wp-calendar td,
.widget_calendar #wp-calendar th {
  text-align: center; }

.widget_calendar #wp-calendar caption {
  font-weight: 500;
  padding: 5px 0 5px 0;
  text-transform: uppercase; }

.widget_calendar #wp-calendar th {
  background: #f4f4f4;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  font-weight: bold; }

.widget_calendar #wp-calendar tfoot td {
  background: #f4f4f4;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc; }

/*--------------------------------------------------------------
## Plugins
--------------------------------------------------------------*/
/* Hide JetPack's sharing module from pages with posts */
.page-posts .sharedaddy {
  display: none; }

/* Get rid of the smiley face when using Jetpack stats */
img.wp-smiley {
  display: inline-block; }

/*
 * Format Jetpack infinite scroll feature. These are globally hidden elements when
 * infinite scroll is supported and in use. Older / Newer Posts Navigation is always
 * hidden. Theme Footer and Page Bottom widget area are hidden while scrolling.
 */
.infinite-scroll .paging-navigation,
.infinite-scroll.neverending .site-footer,
.infinite-scroll.neverending .sidebar-pagebottom {
  display: none; }

/*
 * When Infinite Scroll has reached its end we need to re-display elements that
 * were hidden before (via .neverending)
