.pagination > li > a:hover,
.pagination > li > span:hover,
.pagination > li > a:focus,
.pagination > li > span:focus,
span.page-numbers.current {
  z-index: 2;
  color: #fff;
  background-color: #16a085;
  border-color: #16a085; }

/* Force search field to not be rounded on iOS devices */
input.search-field {
  -webkit-appearance: none !important;
  border-radius: 0 !important; }

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
/*
 * Generically style the links here. Set colors below to make it easier for users to
 * override.
 */
a {
  word-wrap: break-word;
  transition: color .1s ease-in, background .1s ease-in; }

a:hover,
a:focus {
  text-decoration: none;
  outline: 0; }

a:before,
a:after,
a:hover i:before,
a:focus i:before {
  transition: color .1s ease-in, background .1s ease-in; }

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
/* Since we lightened the body font, make the site title regular font weight */
.navbar-brand {
  font-weight: 500;
  /*height: 50px; BS already 50px*/
  /* Fixes layout if nav bar wraps before collapsing for mobile */ }

/*
 * Line up the navbar with the header and page content (it's in a container, but not a
 * row)
 */
div.navbar-collapse.collapse {
  margin-left: -15px;
  margin-right: -15px; }

/* Since we lightened the body font, increase font weight on active tab */
.nav-tabs > li.active > a,
.nav-tabs > li.active > a:hover,
.nav-tabs > li.active > a:focus {
  color: inherit;
  font-weight: 500; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
/* Set content (page title) header to midnight blue */
.content-header {
  background-color: #34495e;
  color: #fff; }

.content-header h1,
.content-header h2,
.content-header h3 {
  color: #fff; }

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
/* Set our footer to "almost" black */
.sidebar-footer {
  background-color: #2f2f2f;
  color: #fff; }

.sidebar-footer h1,
.sidebar-footer h2,
.sidebar-footer h3 {
  color: #fff; }

/* Set our "after footer" (footer nav menu and site credits) to "not quite" black */
.after-footer {
  background: #222222;
  color: #fff;
  font-size: 16px; }

/* Set link colors to off white and text to gray for footer and after footer */
.sidebar-footer,
.footer-nav-menu {
  color: #95a5a6; }

.sidebar-footer a:not(.btn),
.footer-nav-menu a:not(.btn) {
  /*.after-footer a:not(.btn) {*/
  color: #f2f2f2; }

.sidebar-footer a:hover:not(.btn),
.sidebar-footer a:focus:not(.btn),
.footer-nav-menu a:hover:not(.btn),
.footer-nav-menu a:focus:not(.btn) {
  /*.after-footer a:hover:not(.btn),
.after-footer a:focus:not(.btn) {*/
  color: #3bc492; }

/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
/* Reduce the size of post entry meta (date, author) */
.entry-meta {
  font-size: 16px; }

/* Color "sticky" (featured) posts */
.sticky .entry-title:after {
  background-color: #1abc9c;
  color: #fff; }

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
/* Reduce the size of comment edit link */
.comment-edit-link {
  font-size: 16px; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Captions and Galleries
--------------------------------------------------------------*/
/* Color and style the Bootstrap carousel captions */
.carousel-caption,
.carousel-caption h4,
.carousel-caption p {
  font-weight: bold; }

.carousel-caption {
  text-shadow: none;
  background-color: #16a085;
  color: #fff;
  opacity: .75;
  font-weight: bold; }

.carousel-caption a {
  color: #fff;
  text-decoration: underline; }

.carousel-caption a:hover,
.carousel-caption a:focus {
  color: #bdc3c7;
  /*silver*/ }

/*--------------------------------------------------------------
# Widgets and Plugins
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Widgets
--------------------------------------------------------------*/
/* Get Jetpack Popular Posts Widget to look like other thumbnail posts */
.widgets-list-layout-links {
  float: left !important;
  margin-left: 15px !important;
  /*margin-left: 1.625em !important;*/ }

/*--------------------------------------------------------------
## Plugins
--------------------------------------------------------------*/
/* Format Jetpack contact form with Bootstrap's form-control tag. */
form.contact-form input[type='text'],
form.contact-form input[type='email'],
form.contact-form textarea {
  display: block;
  width: 98%;
  height: 36px;
  padding: 6px 12px;
  font-size: 16px;
  line-height: 1.428571429;
  color: #a1a1a1;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s; }

form.contact-form input[type='text']:focus,
form.contact-form input[type='email']:focus,
form.contact-form textarea:focus {
  color: #555; }

.site-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0; }
  .site-header a {
    color: #fff; }
    .site-header a:hover {
      text-decoration: underline; }
  .site-header .top-bar-container {
    max-width: rem-calc(1200);
