.site-footer {
  margin-top: 2rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 0.8em;
  background: #e6e6e6; }
  .site-footer .footer-nav .menu li {
    margin-right: 1rem; }
  .site-footer .footer-nav .menu li a {
    padding: 0.2rem 0; }
